import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
import interfaceurl from '@/utils/interface.js'
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: '申请退款' //导航栏文字
			},
			showPanel: false, //是否显示面板
			chooseType: '退款原因', //选择类型，货物状态、退款原因
			optionsList: {
				"货物状态": [{
					text: '未收到货',
					selected: true
				},{
					text: '未收到货',
					selected: false
				}],
				"退款原因": [{
					text: '不喜欢/不想要',
					selected: true
				},{
					text: '空包裹',
					selected: false
				},{
					text: '快递/物流一直未送到',
					selected: false
				},{
					text: '货物破损已拒签',
					selected: false
				}],
			}, //面板数据
			userChoose: {
				status: '',
				reason: ''
			} //用户选填的值
		}
	},
	onShow() {
		
	},
	methods: {
		touch() {},
		setChooseType(type) {
			this.chooseType = type
			this.showPanel = true
		},
		checkItem(chooseType, index) {
			let i = 0
			for(var item of this.optionsList[chooseType]) {
				if(i == index) {
					item.selected = true
				} else {
					item.selected = false
				}
				i ++
			}
		},
		confirm() {
			this.showPanel=false
			this.userChoose.status = this.optionsList["货物状态"].find(element => element.selected).text;
			this.userChoose.reason = this.optionsList["退款原因"].find(element => element.selected).text;
		},
		submit() {
			
		}
	}
}
